html {
  font-size: 16px;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

body {
  overflow-x: hidden;
}

a {
  text-decoration: none;
}

ul {
  list-style-type: none;
}

.flex {
  display: flex;
}
.flex-jc-ctr {
  justify-content: center;
}
.flex-jc-sb {
  justify-content: space-between;
}
.flex-jc-sa {
  justify-content: space-around;
}
.flex-ai-ctr {
  align-items: center;
}
.flex-fd-col {
  flex-direction: column;
}

.grid {
  display: grid;
}
.grid-jc-ctr {
  justify-content: center;
}
.grid-pi-ctr {
  place-items: center;
}

body {
  width: 100vw;
  height: 100vh;
  background-color: hsl(224deg, 23%, 55%);
}

h1 {
  color: hsl(223deg, 47%, 23%);
  font-family: sans-serif;
}